Vue.component('vue-foot-inner', {
  props: ["error_type", "direction", "radio_type"],
  template: `
    <div class="foot-inner" style="display: flex;">
      <div class="add_stock" v-show="direction == 'buy'" style="padding-left: 24px;clear: both;">
        <input id="add_stock_code" style="padding: 0 5px;border-radius: 3px;border: 1px solid #ccc;" :value="displayStockCode(stock_code)" placeholder=" ..." pattern="^(\d{6}\.(SZ|SH)|\d{5}\.(HK))$" focus-class="active" active-slide="#magic-suggest" >
        <div class="magic-suggest-wrap" data-src="|getMagicSuggest" display="none"></div>
        <button @click="addStock"></button>
      </div>

      <div class="modify_stock" style="flex: 1;text-align: right;">
        <template v-if="direction=='sell'">
          <label @click="radio_change('total')">
            <input style="border-radius: 3px;" name="modify_method" type="radio" checked />
            
            <vue-cell-default :header_data="{class: 'vue_text_default'}" ref="total" placeholder="" @input_blur="input_blur" showtype="input_percentage" index="none" name="change_price_total" checktype="true" class_name="vue_input_default"></vue-cell-default>
          </label>
          <label>
            <input style="border-radius: 3px;" name="modify_method" type="radio" @click="radio_change('equal')" />
            
            <vue-cell-default :header_data="{class: 'vue_text_default'}" ref="equal" placeholder="" @input_blur="input_blur" showtype="input_percentage" index="none" name="change_price_position" checktype="true" class_name="vue_input_default"></vue-cell-default>
          </label>
        </template>
        <template v-if="direction=='buy'">
          <label @click="radio_change('equal')">
            <input name="modify_method" type="radio" style="display:none;border-radius: 3px;"/>
            
            <vue-cell-default :header_data="{class: 'vue_text_default'}" placeholder="" @input_blur="input_blur" showtype="input_percentage" index="none" name="change_price_position" checktype="true" class_name="vue_input_default"></vue-cell-default>
          </label>
        </template>
      </div>

      <div class="form_stock" style="margin-right: 20px;clear: both;">
        <button v-show="direction=='buy'" class="delal_btn_buy" @click="submit_list"></button>
        <button v-show="direction!='buy'" class="delal_btn_sell" @click="submit_list"></button>
        <button class="adjustment_btn" @click="onec_adjustment" style="display:none"></button>
        <span v-show="error_type"></span>
      </div>

    </div>
  `,
  data: function() {
    return {
      set_amount_type: "part",
      equal_show: false,
      target_show: false,
      stock_code: '',
    }
  },
  watch: {
    radio_type(name) {
      if(this.$root.direction != "buy"){
        this.$nextTick(function() {
          if (name == "total") {
            this.$refs.total.my_showtype = "input";
            this.$refs.equal.my_showtype = "readyonly";
            this.$refs.equal.readyonly_placeholder = "";
            this.$refs.equal.input_val = ''
          }
          if (this.$root.direction == "sell") {
            if (name == "equal") {
              this.$refs.equal.my_showtype = "input";
              this.$refs.total.my_showtype = "readyonly";
              this.$refs.total.readyonly_placeholder = "";
            }
          }
          this.$refs.equal.input_val = '';
          this.$refs.total.input_val = '';
        })
      }
    }
  },
  methods: {
    displayStockCode: function(val) {
      if ('marketA' == market) {
        return val
      } else if ('marketHSH' == market) {
        return val.replace('SH', '');
      } else if ('marketHSZ' == market) {
        return val.replace('SZ', '');
      }
    },
    input_blur(name, val) {
      if (name == "change_price_target") {
        this.$emit("change_price_target", val);
      }
      if (name == "change_price_position") {
        this.$emit("change_price_position", val);
      }
      if (name == "change_price_total") {
        this.$emit('change_price_total', val)
      }
    },
    radio_change: function(name) {
      //input
      if (name == "total") {
        this.$emit('change_radio_type', "total")
      }

      if (name == "equal") {
        this.$emit('change_radio_type', "equal")
      }
    },
    addStock: function() {
      let _this = this;
      if (!this.stock_code.length) {
        $.omsAlert('', false);
        return;
      }
      var stock_id = this.stock_code;
      var url = (window.REQUEST_PREFIX || '') + '/user/stock-follow/add';

      $.post(url, {
        stock_id: this.stock_code
      }).done(function(res) {
        if (res.code == 0) {
          $.omsAlert(' ' + stock_id + ' ');
          var tmpLi = $('.multi-stocks-section').find('.magic-suggest>li');
          var stock_name = '';
          tmpLi.each(function() {
            var arr = $(this).html().split(' &nbsp; ');
            if (arr[0] == stock_id) {
              stock_name = arr[1];
            }
          });
          $(window).trigger({
            type: 'create_order:multi_stocks:add_stock',
            stock: {
              stock_id: stock_id,
              stock_name: stock_name
            }
          });
          _this.stock_code = '';
        } else {
          res.code == 502204 ? $.omsAlert(res.msg, false) : $.failNotice(url, res);
        }
      }).fail($.failNotice.bind(null, url)).always(function() {});
    },
    submit_list: function() {
      //tbody 
      this.$emit('submit_list');
    },
    onec_adjustment: function() {
      //tobody
      this.$emit('adjustment');
    }
  },
  mounted() {
    this.$on('addStock', 'addStock');
    let self = this;
    $(function() {
      $('.multi-stocks-section').find('.magic-suggest-wrap').render();
      $('.multi-stocks-section').on('stock_code:suggest', function(event) {
        var stock = event.stock;

        $('.multi-stocks-section').find('#add_stock_code').val(stock.stock_code + '.' + stock.exchange.slice(0, 2)).change();
        self.stock_code = stock.stock_id;
      });
    });
    $(window).on('stock:add_follow', function(event) {
      var stock = event.stock;
      // $('.multi-stocks-section').find('input').val(stock.stock_id).change();
      // self.stock_code = stock.stock_id;
      // self.$emit('addStock');
      var url = (window.REQUEST_PREFIX || '') + '/user/stock-follow/add';
      var stock_id = stock.stock_id;
      var stock_name = stock.stock_name;
      $.post(url, {
        stock_id: stock_id
      }).done(function(res) {
        if (res.code == 0) {
          $.omsAlert(' ' + stock_id + ' ');
          $(window).trigger({
            type: 'create_order:multi_stocks:add_stock',
            stock: {
              stock_id: stock_id,
              stock_name: stock_name
            }
          });
        } else {
          res.code == 502204 ? $.omsAlert(res.msg, false) : $.failNotice(url, res);
        }
      }).fail($.failNotice.bind(null, url)).always(function() {});
    });

    $(window).on('order_create:market:changed', function(event) {
      var market = event.market; //
      $('.multi-stocks-section').find('input#add_stock_code').attr('data-market', market);
      $('.multi-stocks-section').find('.magic-suggest-wrap').render();
    });
    // 
    $(window).on('order_create:deal_method:changed', function(event) {
      // market
      $('.multi-stocks-section').find('input#add_stock_code').attr('data-market', market);
      $('.multi-stocks-section').find('.magic-suggest-wrap').render();

    });
  }
})

